.info3-container{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    transition: 1s;
    left:0;
    }
    
    .info3-container-start{
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width: 100%;
        height: 100vh;
        transition: 1s;
    }

    .info3-wrap{
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
        width:90%;
        height:80%;
        padding:0;
        color:white;
    }
    
    .info3-wrapper{
        display:flex;
        flex-direction: row;
        align-items: center;
        border-style: solid;
        border-radius: 0px;
        width:100%;
        height:90%;
        padding:0;
        color:white;
        overflow: hidden;
        background-color: rgba(0,0,0,0.5);
    }
    
    .image-box{
        position:relative;
        width: 50%;
        height: 100%;
    }
    
    .info-image{
        width:100%;
        height:100%;
        border-radius: 0px;
    }
    
    .info3-1{
        position: relative;
        width:50%;
        height: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        transition: 1s; 
        padding: 0px;
    }
    
    .text{
        width: 70%;
        font-size: 1.2vw;
        margin-bottom: 0;
        text-align: center;
        opacity: 100%;
    }
    
    .heading3{
        font-size:40px;
        margin-bottom: 0;
        float: right;
        padding-bottom:10px; 
    }

    @media(max-width:1300px){
        .info3-wrapper{
            display:flex;
            flex-direction: row;
            align-items: center;
            height: 90%;
        }
    
        .info3-1{
          width:50%;
          height:100%;
        }
    
        .image-box{
          width:50%;
          height:100%;
        }

        .text{
            font-size:1.3vw;
            float: right;
            margin: 0;
            padding-bottom: 10px;
        }
    }
    
    @media(max-width:700px){
        .info3-wrapper{
            display:flex;
            flex-direction: column;
            align-items: center;
            height: 90%;
        }
    
        .info3-1{
          width:100%;
          height:100%;
        }
    
        .image-box{
          width:50%;
          height:100%;
        }

        .text{
            font-size:2vw;
            
            float: right;
            margin: 0;
            padding-bottom: 10px;
        }

        .heading3{
            font-size: 25px;
        }
    }