.info1-container{
position: relative;
width: 100%;
height: 100vh;
transition: 1s;
left:0;
}

.info1-container-start{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    transition: 1s;
}

.info1-wrap{
    position: absolute;
    left:50%;
    top:50%;
    transform: translate(-50%,-50%);
    width:90%;
    height:80%;
    padding:0;
    color:white;
}

.info1-wrapper{
    position: absolute;
    left:50%;
    top:50%;
    transform: translate(-50%,-50%);
    display:flex;
    flex-direction: row;
    align-items: center;
    border-style: solid;
    border-radius: 0px;
    width:100%;
    height:80%;
    padding:0;
    color:white;
    background-color: rgba(0,0,0,0.5);  
}

.info1-wrapper:hover .scroll-container{
  display: flex;
}

.image-box-1{
    position:relative;
    width: 50%;
    height: 100%;
    overflow:hidden;
}

.info-image-1{
    width:100%;
    height:100%;
    min-height: 100%;
    min-width: 100%;
    border-radius: 0px;
    transform: scale(2.0);
}

.info1-1{
    position: relative;
    width:50%;
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: 1s; 
    padding: 0;
}

.text{
    width: 70%;
    font-size: 20px;
    margin-bottom: 20px;
    text-align: center;
    padding:0;
}

.heading1{
    font-size:40px;
    margin-bottom: 0;
    margin: 0;
    padding:0;
    float: right;
    color:white;  
}

@media(max-width:1300px){
    .info1-wrapper{
        display:flex;
        flex-direction: row;
        align-items: center;
        height: 80%;
    }

    .info1-1{
      width:50%;
      height:100%;
    }

    .image-box-1{
      width:50%;
      height:100%;
    }

    .info-image-1{
        transform:scale(3.0);
    }

    .text{
        font-size:1.4vw;
        float: right;
        margin: 0;
        padding-bottom: 10px;
    }
}

@media(max-width:700px){

    .image-box-1{display: none;}

    .info1-wrapper{
        display:flex;
        flex-direction: column;
        align-items: center;
        height: 80%;
    }

    .info1-1{
      width:90%;
      height:100%;
    }

    .image-box-1{
      width:100%;
      height:50%;
    }

    .heading1{
        font-size: 25px;
    }
}

.link{
    position: relative;
    top: 20px;
    padding: 10px;
    color: white;
    text-decoration: none;
    border-style: solid;
    border-width: 1px;
}

.link:hover{
    background-color: white;
    color:black;
}